@media screen and (max-width: 1024px) {
    //header
    .headerDay,
        .headerNight{
            position: relative;
            .themeBtns{
                img{
                    width: 3cqw;
                }
            }
        }
        .menu{
            position: absolute;
        }
    //main
    .main{
        margin-top: 15cqh;
        
        .tittleBlock{
            justify-content: space-around;

            .homeTittle{
                width: 35cqw;
            }
        }
        .homeSwiper{
            width: 25cqw;
            margin: 0 10cqw;
        }
        .parallaxTittle{
            font-size: 4cqw;
           }
        
           .textBlock{
            padding: 2cqh ;
           }
           .aboutCocktail{
               .container{
                width: 95cqw;
                    .itemBlock{
                        .itemTittleRight,
                        .itemTittleLeft{
                            h1{
                                width: 30cqw;
                                margin: 3cqh 0;
                            }
                        }
                        .textRight,
                        .textLeft{
                            p{
                            line-height: 3cqw;  
                            padding: 5cqh;
                            }
                        }
                        
                    }
               }


           }
    }
    //ingredient

    .ingredientSection{
        // grid-template-columns: repeat(2, minmax(0, 1fr));
        // gap: 1rem;
        // .ingredientImg{
            
        // }
        .ingredientBtn{
            width: 25cqw;
        }
    }







}

  @media screen and (max-width: 768px){
    .themeBtns{
        img{
            width: 4cqw !important ;
        }
    }
    .tittleBlock{
        flex-wrap: unset;
        // flex-direction: column-reverse;
        .homeTittle{
            line-height: unset;
            width: 50cqw !important;
            .tittleText{
                width: 45cqw;
            // // font-size: 4cqw;

            }
        }
    }
    .aboutCocktail{
        width: -webkit-fill-available;
        .container{
            width: 85cqw !important;
            margin: 0 auto;
             .itemBlock{
                 .itemTittleRight,
                 .itemTittleLeft{
                     h1{

                         width: 40cqw !important;
                     }
                 }
                 .rightImg,
                 .leftImg{
                    display: flex;
                    align-items: center;
                 }
                 
             }
        }


    }
    // cocktail 
    .cocktailPage{
        .container{
            width: 90cqw;
            .alcohol,
            .non-alcohol{
                .info-block{
                    right: unset;
                    margin: 0 auto;
                }
            }
        }
    }
    .listBtn{
        width: inherit;
        padding: 0 1cqh;
        font-size: 1.5cqw;
    }
    .listCard{
        min-height: 30cqh;
    }
    .nameCocktail{
        margin-left: 0;
        margin-right: 0;
        span{
            width: fit-content;
            font-size: 2cqw;

        }
    }
    .letterDrink{
        width: 90cqw;

    }
    .modalBg{
        .modalCloseBtn{
            width: 7cqw;
        }
        .modalCard{
            width: 75cqw;
        }
        .modal{
            width: 98cqw;
        }

    }
    .drink{
        .container{
            padding-top: 0;

        .drinkWrapper{
            display: flex;
            flex-direction: column-reverse;
        }
        .drinkCard{
            max-width: -webkit-fill-available;
            width: 90cqw;
        }

        }
        .ingredientWrap{
            text-align: center;
            .card{
                padding: 0;
                padding-bottom: 2vh;
    
            }

        }
        
        
    }
    
    
  }
  @media screen and (max-width: 600px) {
    .themeBtns{
        img{
            width: 5cqw !important ;
        }
    }
   
    .itemBlock{
        .itemTittleRight,
        .itemTittleLeft{
            h1{
                padding: 2cqh;
                width: 40cqw !important;
                font-size: 6cqw;
            }
        }
        .textRight,
        .textLeft{
            p{
                font-size: 2cqw;
                line-height: 3cqh !important;
            padding: 1cqh !important;
            }
        }
        
    }

    // cocktail
    .alcohol,
    .non-alcohol{
        justify-content: end;
        position: unset;
        .info-block{
            width: 80cqw;
            .description{
                // background-image: url('../img/alcohol.jpeg');
                padding: 2cqh 0;
                // width: 70cqw;
                width: fit-content;
                p{
                    border-radius: 5cqw;
                    margin: 5cqw;
                    padding: 2cqw;
                }

            }
        }
    }
    .alcohol{
        .description{
            background-image: url('../img/alcohol.jpeg');
            background-size: cover;
            color: bisque;
            // margin: 0;
            p{
                background-color: rgba(0,0,0, 0.6);
            }

        }
    }
    .non-alcohol{
        .description{
            background-image: url('../img/non-alco.jpeg');
            background-size: cover;
            p{
                background-color: rgba(255,255,255, 0.7);
            }

        }
    }
    .main-img{
        display: none;
    }
    .letterDrinkImg{
        width: 50cqw;
    }
    .favoriteBtn{
        button{
            img{
                width: 5cqw;
            }
        }
    }
    







    //ingredient

    .ingredientSection{
        grid-template-columns: repeat(2, minmax(0, 1fr));
        gap: 1rem;
        .ingredientBtn{

        }
    }


  }

  @media screen and (max-width: 600px) {
    .main{
        margin-top: 4cqh;
    }
    .headerDay,
    .headerNight{
        border-bottom-width: inherit;
        background-image: none;
        opacity: unset;
    }
    .button-group{
        align-items: baseline;
        position: relative;
        // top: 2cqh;
        .menuBtn{
            position: relative;
            top: 2cqh;
            display: block;
            .btnWrapper {
                top: 0;
            }
        }
        .themeBtns{
            position: relative;
            top: 2cqh;

        }
        .burgerBtn,
        .themeBtn{
        width: 7cqw !important;
    }
    }
    

    .menu{
        display: block;
        width: 100vw;
        height: 100cqh;
        position: absolute;
        left: -100vw;
        // background-color: rgb(186, 235, 252);
        transition: all ease 1s;
        z-index: 10;
        .mobileMenuClose{
            display: block;
            .buttonClose{
                margin-left: 5cqw;
                z-index: 10;
            }
        }
        ul{
            background-color: rgb(186, 235, 252);
            display: flex;
            height: -webkit-fill-available;
            justify-content: center;
            flex-direction: column;
            position: absolute;
            width: 50vw;
            top: 0;

            li{
                display: block;
                font-size: 5cqw;
                line-height: 5cqh;
                background-color: rgb(59 130 246);
                // border: 1px solid rgb(0, 0, 0);
                border-radius: 1cqw;
                width: 35cqw;
                margin: 0 auto 3cqh;
                color: white;
                font-size: 3cqw;
                a{
                    display: block;
                }
            }
        }
        
         
    }
    .menuActive{
        left: 0;
        transition: all ease 1s;
        backdrop-filter: blur(5px);
        

        
        

    }
    
    .tittleBlock{
        padding-bottom: 15cqh;
        .homeTittle{
            padding: 0;
            min-width: 50cqw;
            .tittleText{
                // margin: 0 0 2cqh 0;
                width: 45cqw;
                h2{
                    line-height: 5cqh;
                    font-size: 4cqw;
                    span{
                    font-size: 4cqw;

                    }
                }
            }
        }
        .homeSwiper{
           max-width: 30cqw;
            .swiper-3d {
                perspective: 130px;
            }
            .swiper-slide{
                img{
                    width: 40cqw;
                }
            }
        }
    }
    .sectionBtn{
        position: absolute;
        margin-top: 5cqh;
        display: flex;
        justify-content: space-around;
        width: -webkit-fill-available;
        button{
            display: inline-block;
            padding: 3cqw;
            margin: 2cqw;
        }
    }
    .textBlock{
        padding: 0 2cqh 10cqh 2cqh;
    }
    .parallaxTittle{
        font-size: 4cqw;
    }
    .searchBlock{
        position: fixed;
        transition: all ease 0.7s;
        left: 105vw;

        top: 85vh;
        z-index: 123;
    }
    .searchActive{
        transition: all ease 0.7s;

        left: 5vw;

    }







    
    









 

   }

