            // grid-template-columns: repeat(2, minmax(0, 1fr));
            gap: 1cqw;
            
            // .ingredientImg{
                
            // }
            .ingredientBtn{
                width: 25cqw;
            }
    
       }

    }

   //ingredient

   
}